.language {
  display: inline-block;
  float: right; }

.menu {
  display: inline-block;
  margin-top: 40px;
  margin-bottom: 40px;
  font-size: 20px;
  list-style: none;
  padding: 0; }
  .menu li {
    display: inline-block;
