    border: 0;
    margin: 0 40px 0 0;
    padding: 0;
    cursor: pointer; }
  .menu .active {
    border-bottom: 5px solid #E50012; }
  .menu .sub-menu .active {
    border-bottom: 2px solid #E50012;
    padding-bottom: 2px; }

.sub-menu {
  padding-top: 20px; }
  .sub-menu ul {
    display: none; }
  .sub-menu ul {
    margin-top: 40px;
    margin-bottom: 40px;
    font-size: 14px;
    list-style: none;
    padding: 0; }
  .sub-menu li {
    margin-right: 20px;
    display: inline-block;
    color: #5f5f5f;
    cursor: pointer; }

.main {
  min-height: 510px;
