  .main h2 {
    font-size: 24px;
    color: #fff;
    font-weight: 400;
    margin-top: 24px; }
  .main p {
    font-size: 20px;
    color: #fff;
    line-height: 30px; }
  .main ol {
    display: inline-flex;
    flex-direction: column;
    padding-inline-start: 20px;
    margin-bottom: 30px; }
    .main ol li {
      color: #fff;
