      padding: 0;
      margin: 0;
      font-size: 18px;
      line-height: 1.75; }
  .main .sub-content p {
    margin-bottom: 30px; }

.about {
  background-image: url("./assets/img/bg-02.jpg"); }
  .about p {
    margin-top: 130px; }

.contact {
